@charset "UTF-8";
/* リキッドレイアウト対応 */
:root {
  --padding-pc: 25px;
  --padding-sp: 20px;
  --base-font-family: "Gothic A1", sans-serif;
  --second-font-family: "Shippori Mincho", serif;
  --zen-old-mincho: "Zen Old Mincho", serif;
  --darumadrop-one: "Darumadrop One", cursive;
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --black: #000;
  --white: #fff;
}

body {
  font-family: var(--base-font-family);
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
html {
  font-size: 16px;
}
@media (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  a[href^="tel:"] {
    pointer-events: all;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  height: 100%;
  object-fit: cover;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

html {
  overflow-wrap: break-word;
}

.inner {
  width: 100%;
  max-width: calc(1100px + var(--padding-pc) * 2);
  padding-right: var(--padding-pc);
  padding-left: var(--padding-pc);
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .inner {
    max-width: calc(1100px + var(--padding-sp) * 2);
    padding-right: var(--padding-sp);
    padding-left: var(--padding-sp);
  }
}

.header__nav {
  display: flex;
  align-items: center;
  gap: 4rem;
  line-height: 1.2;
  padding: 1.5rem 5rem;
  border-radius: 62.5rem;
  box-shadow: 0px 0.25rem 0.9375rem 0px rgba(37, 123, 6, 0.27);
  width: 100%;
}
@media screen and (max-width: 767px) {
  .header__nav {
    gap: 2rem;
    padding: 1rem 2.5rem;
  }
}

.header__list {
  display: flex;
  align-items: center;
  gap: 4rem;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .header__list {
    gap: 2rem;
    flex-wrap: wrap;
  }
}

.header__item {
  flex-shrink: 0;
}

.header__link {
  display: block;
  font-size: 1.125rem;
  font-weight: var(--regular);
  font-family: "Zen Old Mincho", serif;
  color: #293707;
  line-height: 1.2;
  text-decoration: none;
  white-space: nowrap;
}
@media screen and (max-width: 767px) {
  .header__link {
    font-size: 1rem;
  }
}

/* メインビジュアル */
.hero {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 83.75rem;
  background-color: #f3f2e9;
  overflow: hidden;
}

.hero__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../images/common/mv-bg.jpg");
  background-repeat: repeat;
  background-size: 8.61875rem 7.7875rem;
  background-position: top left;
  z-index: 1;
}

.hero__circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 62.5rem;
  height: 62.5rem;
  background-image: url("../images/common/mv.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 2;
}

/* スクロールテキスト */
.hero__scroll-text {
  position: absolute;
  left: -14.625rem;
  top: 45.9375rem;
  z-index: 3;
  width: 100%;
  max-width: 125rem;
}

.hero__scroll-row {
  display: flex;
  align-items: center;
  gap: 1.33175rem;
  border-top: 0.08325rem solid #fff;
  border-bottom: 0.08325rem solid #fff;
  padding: 1.25rem 0;
  white-space: nowrap;
}
.hero__scroll-row--bottom {
  border-top: none;
}

.hero__scroll-item {
  font-family: var(--second-font-family);
  font-size: 5.092125rem;
  line-height: 1.5;
  color: #fff;
  white-space: nowrap;
}

.hero__scroll-icon {
  display: inline-block;
  width: 9.375rem;
  height: 5rem;
  background-image: url("../images/common/dummy.jpg");
  background-size: cover;
  background-position: center;
  flex-shrink: 0;
}

/* ロゴ */
.hero__logo {
  position: absolute;
  top: 10.8125rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 4;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.25rem;
}

.hero__logo-circle {
  width: 15rem;
  height: 15rem;
  background-image: url("../images/common/mv.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero__logo-icon {
  width: 8.174625rem;
  height: 7.0709375rem;
  object-fit: contain;
}

.hero__logo-text {
  font-family: var(--darumadrop-one);
  font-size: 2.25rem;
  line-height: 1.5;
  color: #fff;
  margin: 0;
  font-weight: var(--regular);
}

/* 装飾イラスト */
.hero__deco {
  position: absolute;
  z-index: 3;
}
.hero__deco--left {
  top: 8.859375rem;
  left: 31.7325rem;
}
.hero__deco--right {
  top: 9.92375rem;
  right: 32.980625rem;
}

.hero__deco-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* はじめましてセクション */
.intro {
  position: relative;
  width: 100%;
  padding: 6.25rem 0;
  background-color: #f3f2e9;
  z-index: 5;
}

.intro__inner {
  max-width: 41.625rem;
  margin: 0 auto;
  padding: 0 var(--padding-pc);
  text-align: center;
}

.intro__title {
  font-family: var(--darumadrop-one);
  font-size: 3rem;
  line-height: 1.5;
  color: #6d8e1b;
  margin: 0 0 2.5rem 0;
  font-weight: var(--regular);
}

.intro__text {
  font-family: var(--base-font-family);
  font-size: 1.5rem;
  line-height: 2.1;
  color: #282828;
}
.intro__text p {
  margin: 0;
  white-space: pre-wrap;
}